import axios from "axios";

const state = {
  educations: [],
};

const getters = {
  educations: (state) => state.educations,
};

const actions = {
  async fetchEducations({ commit }) {
    const res = await axios.get(
      "https://agungsepruloh.herokuapp.com/educations"
    );
    commit("setEducations", res.data);
  },
};

const mutations = {
  setEducations: (state, educations) => (state.educations = educations),
};

export default {
  state,
  getters,
  actions,
  mutations,
};
