import axios from "axios";

const state = {
  experiences: [],
};

const getters = {
  experiences: (state) => state.experiences,
};

const actions = {
  async fetchExperiences({ commit }) {
    const res = await axios.get(
      "https://agungsepruloh.herokuapp.com/experiences"
    );
    commit("setExperiences", res.data);
  },
};

const mutations = {
  setExperiences: (state, experiences) => (state.experiences = experiences),
};

export default {
  state,
  getters,
  actions,
  mutations,
};
