import axios from "axios";

const state = {
  interests: [],
};

const getters = {
  interests: (state) => state.interests,
};

const actions = {
  async fetchInterests({ commit }) {
    const res = await axios.get(
      "https://agungsepruloh.herokuapp.com/interests"
    );
    commit("setInterests", res.data);
  },
};

const mutations = {
  setInterests: (state, interests) => (state.interests = interests),
};

export default {
  state,
  getters,
  actions,
  mutations,
};
